
/* =============================== 섹션 이름  */
.basis-section{
   &.infoaction-section { margin-top: 100px;   
      .tb1_1{font-size: 18px; font-weight: 500; line-height: 1.78;
         li, span, em {font-size: inherit; font-weight: inherit;}
      } 
   }
   @media all and (max-width:$G-wMobile)  {
     &.infoaction-section { margin-top: 30px; 
      .tb1_1{font-size: 13px; line-height: 1.7;
         li, span, em {font-size: inherit; font-weight: inherit;}
      }
    }
   }
 }
 
 
 
 
 
 /* =============================== 경매안내 */
 .content-panel{
   &.type_panel-infoaction { 
      .info_dot_list{ 
         &.step0_list{margin-top: 40px;}
         &.mark_dot-list li{ padding-left: 0.7em;}
         &.mark_dot-list li:after{ top: 13px;}
         em{ color:#333; font-weight: 700;}
      }
      .tab_move-bar{
         &.online_tab ul{
            li{ width: calc( 100% / 5); }
         }
         ul{ margin-top: 100px; display: flex; border-top: 1px solid #dedede; border-left: 1px solid #dedede;
            li{ width: calc( 100% / 6); 
               .li-inner{ border-right: 1px solid #dedede; border-bottom: 1px solid #dedede;
               }
               a{ display: block; padding: 15px 20px; border: 1px solid transparent; 
                  .teb_txt{font-size: 14px; font-weight: 700; color: $colorP;}
                  .teb_title{font-weight: 700; color: #333;}
               }
               &:hover { 
                  .li-inner{ border-bottom: 1px solid #4dada7; }
                  a { border-bottom: 1px solid #4dada7; }
               }
            }
            
         }
      }
      .article-body{ 
         .step-area{
            .step_stage{ margin-top: 55px;
               .step_title{
                  .teb_txt{font-weight: 700; color: $colorS;}
                  .teb_title{color: $colorB2;}
               }
               .step_con{ margin-top: 20px;
                  .con_header{
                     ol{
                        li{ font-weight: inherit;
                           span{
                              em{ color: $colorP;}
                           }
                        }  
                     }
                     .inquiry_txt{position: relative; padding-left: 30px; margin-top: 35px;
                        i{ position: absolute; top: 6px; left: 0;}
                     }
                     .means_txt_wrap{ display: flex;
                        .btn_box{ display: flex; align-items: end; padding-left: 20px;
                           .btn{ height: 42px; width: 170px;
                              span{line-height: 30px;}
                           }
                        }
                     }
                     .means_txt_wrap_online{ margin-top: 40px;
                        .inner{ display: flex; justify-content: space-between; align-items: center;
                           .inquiry_txt{margin-top: 0;}
                        }
                        .btn_box{ 
                           a{ min-width: 360px;}
                        }
                     }
                  }
                  .con_body{ margin-top: 35px;
                     .join_box{
                        .join_box_online { display: block;
                           li{ width: 100%; padding-top: 40px;}
                           .box_body{ margin-top: 0px;
                            .txt_area{ 
                              .btn_set{ justify-content: center;
                                 .btn_box{padding: 0 10px;}
                              }
                            }
                           }
                        }
                        >ul{display: flex;
                           >li{ border: 1px solid #dedede; padding: 60px 40px; width: calc( 100% / 2 );
                              .box_header{ text-align: center;
                                 img{width: 142px;}
                                 p{color: $colorB2; margin-top: 15px;
                                    &.onlime_item{ margin-top: 20px;}
                                 }
                              } 
                              .box_body{ margin-top: 50px;
                                 &.box_body_online{ margin-top: 15px; text-align: center;
                                    em{ color: $colorP;}
                                 }
                                 .txt_area{ 
                                    .btn_box{ margin-top: 20px;
                                       a{ min-width: 170px;}
                                    }
                                 }
                                 .explanation_area{ margin-top: 40px;
                                    dl{
                                       dt{ position: relative;  color: $colorB2;
                                          &.exception_txt{ color: $colorP;
                                             span{padding-left: 0;}
                                          }
                                          i{ position: absolute; top: 6px;}
                                          span{ padding-left: 28px; font-weight: 700;}
                                       }
                                       dd{
                                          a{ display: inline-block; padding-left: 4px;}
                                       }
                                    }
                                    dl + dl{ margin-top: 25px;}
                                 }
                              }
                           }
                           >li + li{ margin-left: 20px;}
                        }
                     }
                     .preview_box{
                        .inner{ display: flex; border: 1px solid #dedede; padding: 40px 50px; 
                           .step2_2{padding-left: 40px; 
                              li{font-weight: 500;}
                           }
                        }
                     }
                     .means_box_wrap{
                        .means_box{ 
                           ul{ display: flex; flex-wrap: wrap; width: calc( 100% + 20px ); margin-left: -10px;
                              li{ width:calc( 100% /2); padding: 10px 10px;
                                 .inner{ display: flex; border: 1px solid #dedede; padding: 60px 60px; height: 100%;
                                    .img_box{ 
                                       img{width: 142px; }
                                    }
                                    dl{padding-left: 35px; width: calc( 100% - 142px );
                                       dt{ color: $colorB2;}
                                       dd{ margin-top: 10px;}
                                       p{ border-top: 1px solid #dedede; color: $colorP; padding-top: 25px; margin-top: 25px;}
                                    }
                                 }
                              }
                           }
                           
                        }
                     }
                     .reservation_box{
                        .txt_area{
                           ul{
                              li{ font-weight: 500;}
                              li + li{ margin-top: 15px;}
                           }
                        }
                     }
                     .identify_box{ margin-top: -15px;
                        ol>li + li {margin-top: 40px;}
                        dl{
                           dt{ color: $colorB2;}
                           dd{ margin-top: 15px;
                              ul{
                                 li{font-weight: 500;
                                    em{ color: $colorB2; font-weight: 700;}
                                 }
                              }
                              .txt_box{ border: 1px solid #dedede; margin-top: 25px; padding: 32px 40px;
                                 p{color: $colorB2; font-weight: 700;}
                                 em{ font-weight: 700;}
                              }
                              .inquiry_txt{position: relative; padding-left: 30px; margin-top: 15px;
                                 em{ color: $colorP}
                                 i{ position: absolute; top: 6px; left: 0;}
                              }
                              .btn_box{ margin-top: 20px; 
                                 a{ min-width: 170px;}
                              }
                              .step5{margin-top: 15px;}
                              .txt_box_last{ border: 1px solid #dedede; margin-top: 25px; padding: 17px 40px 32px;
                                 ul li{
                                    a{ display: inline-block;}
                                    em{ color: $colorD;}
                                 } 
                                 .inquiry_txt{
                                    span{ color: $colorB2; font-weight: 700;} 
                                 }
                                 p{ margin-top: 20px;}
                                 .txt_area{ margin-top: 15px;}
                                 .article + .article{ margin-top: 40px;}
                              }
                           }
                        }
                     }
                     .ship_box{
                        .txt_box{ border: 1px solid #dedede; padding: 35px 40px;
                           >p{ color:$colorB2; font-weight: 700;}
                           ol{ margin-top: 20px;
                              li{ font-weight: 500;
                                 em{ font-weight: 700;}
                                 p{ color: $colorP; font-weight: 500; padding-left: 15px;}
                              }
                              li + li {margin-top: 12px;}
                           }
                        }
                        .txt_area{
                           >p{ color: $colorB2; margin-top: 35px; font-weight: 700;}
                           .bottom_txt{ padding-left: 13px;}
                        }
                     }
                     .online_table_box{
                        dt{margin-top: 50px;}
                        dd{ margin-top: 35px;
                           table{
                              th , td{ height: 60px;}
                           }
                        }
                     }
                  }          
               }
            }
         }
      }



     @media all and (max-width:$G-wMobile)  {
      .info_dot_list{ 
         &.step0_list{margin-top: 15px;}
         &.mark_dot-list li{ padding-left: 0.7em;}
         &.mark_dot-list li:after{ top: 9px;}
         em{ color:#333; font-weight: 700;}
      }
      .tab_move-bar{
         &.online_tab ul{
            li{ width: calc( 100% / 2); }
         }
         ul{ margin-top: 40px; flex-wrap:wrap;
            li{ width: calc( 100% / 2); 
               .li-inner{}
               a{ padding: 11px 15px;  
                  .teb_txt{font-size: 12px;}
                  .teb_title{font-size: 14px;}
               }
            } 
         }
      }
      .article-body{ 
         .step-area{
            .step_stage{ margin-top: 25px;
               .step_con{ margin-top: 7px;
                  .con_header{
                     .inquiry_txt{padding-left: 18px; margin-top: 20px;
                        i{ top: 4px; left: 0;}
                     }
                     .means_txt_wrap{ display: block;
                        .btn_box{ display: block; padding-left: 0px; margin-top: 18px;
                           .btn{ height: 32px; width: 140px;
                              span{line-height: 23px;}
                           }
                        }
                     }
                     .means_txt_wrap_online{ margin-top: 10px;
                        .inner{ display: block;
                           .inquiry_txt{margin-top: 0;}
                        }
                        .btn_box{ margin-top: 17px; width: 100%;
                           a{ min-width: 260px; width: 100%;}
                        }
                     }
                  }
                  .con_body{ margin-top: 15px;
                     .join_box{
                        .join_box_online { 
                           li{ padding-top: 25px;}
                           .box_body{ 
                            .txt_area{ 
                              .btn_set{ 
                                 .btn_box{padding: 0 5px; margin-top: 25px;
                                    a{min-width: 130px;}
                                 }
                              }
                            }
                           }
                        }
                        >ul{ display:block;
                           >li{ padding: 25px 15px; width:100%;
                              .box_header{ 
                                 img{width: 71px;}
                                 p{margin-top: 10px;
                                    &.onlime_item{ margin-top: 10px;}
                                 }
                                 
                              } 
                              .box_body{ margin-top: 20px;
                                 .txt_area{ 
                                    .btn_box{ margin-top: 15px;
                                       a{ min-width: 160px;}
                                    }
                                 }
                                 .explanation_area{ margin-top: 15px;
                                    dl{
                                       dt{ position: relative;  color: $colorB2;
                                         
                                         
                                          span{ padding-left: 0px; }
                                       }
                                       dd{
                                          a{ display: inline-block; padding-left: 4px;}
                                       }
                                    }
                                    dl + dl{ margin-top: 15px;}
                                 }
                              }
                           }
                           li + li{ margin-left: 0px; margin-top: 10px;}
                        }
                     }
                     .preview_box{
                        .inner{ display: block; padding: 25px 20px; 
                           .img_box{ text-align: center;
                              img{ width: 71px;}
                           }
                           .step2_2{padding-left: 0px; margin-top: 25px;}
                        }
                     }
                     .means_box_wrap{
                        .means_box{ 
                           ul{ display: block; width: 100%; margin-left: 0px;
                              li{ width: 100%; padding: 0px 0px;
                                 .inner{ display: block; padding: 25px 25px; 
                                    .img_box{ text-align: center;
                                       img{width: 71px;}
                                    }
                                    dl{padding-left: 0px; width: 100% ;
                                       dt{margin-top: 10px; text-align: center;}
                                       dd{ margin-top: 20px;}
                                       p{ padding-top: 15px; margin-top: 15px;}
                                    }
                                 }
                              }
                              li + li {margin-top: 10px;}
                           }
                           
                        }
                     }
                     .reservation_box{
                        .txt_area{
                           ul{
                              li + li{ margin-top: 10px;}
                           }
                        }
                     }
                     .identify_box{ margin-top: -8px;
                        ol>li + li {margin-top: 20px;}
                        dl{
                           dd{ margin-top: 8px;
                              .txt_box{ margin-top: 15px; padding: 20px;
                              }
                              .inquiry_txt{padding-left: 18px; margin-top: 10px;
                                 i{ top: 4px; left: 0;}
                              }
                              .btn_box{ margin-top: 15px; margin-left: 0px;
                                 a{ min-width: 160px;}
                              }
                             
                              .txt_box_last{ margin-top: 15px; padding: 7px 20px 20px;
                                 ul li{
                                    a{ display: inline-block;} 
                                 } 
                                 .inquiry_txt{
                                    i{ top: 3px; }
                                 }
                                 p{  margin-top: 10px;}
                                 .txt_area{ margin-top: 10px;}
                                 .article + .article{ margin-top: 10px;}
                              }
                           }
                        }
                     }
                     .ship_box{
                        .txt_box{ padding: 20px 20px;
                           ol{ margin-top: 10px;
                              li{ 
                                 p{ padding-left: 10px; position: relative; margin-top: 3px;
                                    &:after { content: ""; display: block; width: 3px; height: 3px; 
                                       position: absolute; top:8px; left: 0; 
                                       background-color: $colorP 
                                    }      
                                 }
                              }
                              li + li {margin-top: 18px;}
                           }
                        }
                        .txt_area{
                           >p{ margin-top: 25px; }
                           .bottom_txt{ padding-left: 8px;}
                        }
                     }
                     .online_table_box{
                        dt{margin-top: 30px;}
                        dd{ margin-top: 20px;
                           table{
                              th , td{ height: 45px;}
                           }
                        }
                     }
                  } 
               }
            }
         }
      }
     }
   } 
 }
  /* =============================== 경매안내 팝업 */
 .content-panel{ 
    &.type_panel-infoaction_popup{ margin-top: 100px;
       .content-body{
         p{ font-size: 28px; font-weight: 700; color: $colorB2;}
         ul{ display: flex;  margin-top: 30px; width: calc( 100% + 60px); margin-left: -30px;
            >li:first-child{ width: 38%;}
            >li{ padding: 0 30px;
               .img_01{ margin-top: 40px;}
            }
         }
       }
    }
    @media all and (max-width:$G-wMobile)  {
      &.type_panel-infoaction_popup{ margin-top: 80px;
         .content-body{
           p{ font-size: 18px;}
           ul{ display: block;  margin-top: 10px; width: 100%; margin-left: 0px;
              >li:first-child{ width: 100%;}
              >li{ padding: 0;
               .img_01{ margin-top: 25px; 
                  img{width: 100%;}
               }
               .img_02{ margin: 20px 0; 
                  img{width: 100%;}
               }
               .img_03 img{ width: 100%;}
            }
           }
         }
      }
   }
 }